import { Box, Drawer } from "@mui/material";
import React from "react";
import { useContext } from "react";
import { ThemeContext } from "styled-components";

export function Sidebar({sidebarState, setSidebarState}) {
  const {body, fonts} = useContext(ThemeContext)

return (
  <div>
        <Drawer
          open={sidebarState}
          onClose={() => setSidebarState(!sidebarState)}
        >
        <Box height={'100%'} sx={{backgroundColor: body.primary, color: fonts.color.primary}}>
        eaissssssssssssssssssss
        </Box>
        </Drawer>
  </div>
);
      }