import * as React from "react";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import styled, { ThemeContext } from "styled-components";
import { useContext } from "react";
import { TextField } from "@mui/material";

const bull = (
  <Box
    component="span"
    sx={{ display: "inline-block", mx: "2px", transform: "scale(0.8)" }}
  >
    
  </Box>
);

const Section = styled.section`
  width: 100%;
  padding: 50px 0 0 20px;
`;

export function CardStartNow() {
  const { body, fonts, button } = useContext(ThemeContext);

  return (
    <Section>
      <Card
        sx={{
          width: 270,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <CardContent>
          <Typography variant="h5" component="div" color={fonts.color.title}>
            BlockLabs
          </Typography>
          <Typography sx={{ mb: 1.5 }} color={fonts.color.subtitle}>
            Sem Limites
          </Typography>
          <Typography variant="body2" color={fonts.color.text}>
            Faa transaes utilizando nossa API sem limites e sem custos
          </Typography>
        </CardContent>
        <CardActions style={{ flexDirection: "column" }}>
          <TextField
            color={"secondary"}
            size={"small"}
            id="standard-basic"
            fullWidth
            label="Email"
          />
          <TextField
            color={"secondary"}
            id="outlined-password-input"
            label="Password"
            type="password"
            autoComplete="current-password"
            style={{
              margin: "10px 0",
            }}
            size={"small"}
          />
          <Button
            style={{
              margin: "20px",
              backgroundColor: button.primary,
              color: button.text,
            }}
            fullWidth
            variant="contained"
          >
            Comece agora
          </Button>
        </CardActions>
      </Card>
    </Section>
  );
}
