import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import styled, { ThemeContext } from "styled-components";
import { useContext } from "react";
import { Sidebar } from "../Sidebar/Sidebar";
import { useState } from "react";
import Button from "@mui/material/Button";

const Nav = styled.nav`
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 20px;
  & section {
    width: 350px;
    display: flex;
    justify-content: space-between;
  }
`;

export function AppBarComponent() {
  const { body, fonts } = useContext(ThemeContext);
  const [openSidebar, setOpenSidebar] = useState(false);

  return (
    <Box sx={{ flexGrow: 1, color: fonts.color.primary }}>
      <AppBar position="static" sx={{ backgroundColor: body.primary }}>
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="open drawer"
            sx={{ display: { xs: "block", sm: "none" } }}
            onClick={() => setOpenSidebar(!openSidebar)}
          >
            <MenuIcon />
          </IconButton>
          <IconButton
            size="large"
            color="inherit"
            aria-label="open drawer"
            sx={{ borderRadius: "5px" }}
          >
            <svg
              width="30"
              viewBox="0 0 200 200"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <rect width="200" height="200" fill="white" />
              <rect
                x="46"
                y="42"
                width="50"
                height="50"
                rx="4"
                fill="#0A0B14"
              />
              <rect
                x="46"
                y="100"
                width="50"
                height="50"
                rx="4"
                fill="#121218"
              />
              <rect
                x="104"
                y="100"
                width="50"
                height="50"
                rx="4"
                fill="#0A0B14"
              />
              <rect
                x="104"
                y="42"
                width="50"
                height="50"
                rx="25"
                fill="#EDB72D"
              />
            </svg>
          </IconButton>
          <Nav>
            <section>
              {["Home", "Documentation", "About"].map((value) => {
                return (
                  <Button
                    key={value}
                    variant="text"
                    sx={{
                      flexGrow: 1,
                      color: fonts.color.primary,
                      textTransform: "capitalize",
                      display: {
                        xs: "none",
                        sm: "block",
                      },
                    }}
                  >
                    {value}
                  </Button>
                );
              })}
            </section>
          </Nav>
        </Toolbar>
      </AppBar>
      <Sidebar sidebarState={openSidebar} setSidebarState={setOpenSidebar} />
    </Box>
  );
}
